import React, { FC } from "react";
import styled from "styled-components";

import {
	colors, Margins, widthSizes,
} from "../styleguide";

interface StyledDividerProps extends Margins {
	borderColor?: string
	width?: string
	offset?: number
}
export interface DividerProps extends StyledDividerProps {
	dataQA: string
	className?: string
}

const StyledDivider = styled.div<StyledDividerProps>`
	border-top: ${({ borderColor }) => `1px solid ${borderColor || `${colors.black}1A`}`};
	margin-top: ${({ mt }) => mt};
	margin-bottom: ${({ mb }) => mb};
	margin-left: ${({ ml, offset }) => offset ? `-${offset}px` : ml};
	margin-right: ${({ mr }) => mr};
	margin: ${({ m }) => m};
	width: ${({ width, offset }) => offset ? `calc(100% + ${2 * offset}px)` : width};
`;
const Divider: FC<DividerProps> = ({
	borderColor,
	mt,
	mb,
	mr,
	ml,
	m,
	width = widthSizes.full,
	className,
	dataQA,
	offset,
}) => (
	<StyledDivider
		data-qa={dataQA}
		borderColor={borderColor}
		mt={mt}
		mb={mb}
		ml={ml}
		mr={mr}
		m={m}
		width={width}
		className={className}
		offset={offset}
	/>
);

export default Divider;
